import each from 'lodash/each'
import isestr from './isestr.mjs'
import split from './split.mjs'


/**
 * 
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/sep.test.js Github}
 * @memberOf wsemi
 * @param {String} c 
 * @param {String} t 
 * @returns {Array} 
 * @example
 * sep('1.25 abc  ', ' ')
 * // => ['1.25', 'abc', '']
 */
function sep(c, t) {

    //check
    if (!isestr(c)) {
        return []
    }
    if (!isestr(t)) {
        return []
    }

    let s = split(c, t)
    let r = []
    each(s, function(v) {
        if (isestr(v)) {
            r.push(v)
        }
    })

    return r
}


export default sep
