import genPm from './genPm.mjs'
import isnum from './isnum.mjs'


/**
 * Promise
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/delay.test.js Github}
 * @memberOf wsemi
 * @param {Number} [ms=1] 10ms
 * @returns {Promise}} Promiseresolve
 * @example
 * delay(1000)
 * // => 1s
 */
function delay(ms = 10) {

    //check
    if (!isnum(ms)) {
        ms = 10
    }

    let pm = genPm()
    setTimeout(function() {
        pm.resolve()
    }, ms)
    return pm
}


export default delay
