import isestr from './isestr.mjs'


/**
 * 
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/split.test.js Github}
 * @memberOf wsemi
 * @param {String} c 
 * @param {String} t 
 * @returns {Array} 
 * @example
 * split('1.25 abc  ', ' ')
 * // => ['1.25', 'abc', '', '']
 */
function split(c, t) {

    //check
    if (!isestr(c)) {
        return []
    }
    if (!isestr(t)) {
        return []
    }

    let r = c.split(t)

    return r
}


export default split
